import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Provider from './store/Provider';
// import Home from './pages/Home/Home';
import Tickets from './pages/Tickets/Tickets';
import SingleTicket from './pages/SingleTicket/SingleTicket';
import Layout from './layout/Main/Main';
import Login from './pages/Login/Login';
import Map from './pages/Map/Map';
import './App.css';

const App = () => {
  return (
    <Router>
      <Provider>
        <Switch>
          <Route path="/login" component={Login} />
          <Layout>
            <Route path="/flares" component={Map} />
            {/* <Route path="/flares" component={Home} /> */}
            <Route path="/tickets" component={Tickets} />
            <Route path="/single-ticket/:id" component={SingleTicket} />
          </Layout>
        </Switch>
      </Provider>
    </Router>
  );
};

export default App;
